import React, { useContext } from 'react';
import styled from 'styled-components';
import Context from '../Context';
import Video from './Video';

const HomeScreen = styled.div`
  display: flex;
  height: 100%;
  background-color: #2300e2;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
`;

const ShuffleButton = styled.button`
  filter: drop-shadow(10px 5px 4px #180096);
  border-style: outset;
  border-width: 5px;
  border-color: yellow;
  background-color: yellow;
  padding: 1rem;
  font-size: 2rem;
  color: red;
  width: 100%;
  margin: 1rem;
`;

const shuffleVideos = (n) => {
  const keys = [...Array(97).keys()]
    // Shuffle array
  const shuffled = keys.sort(() => 0.5 - Math.random());

  // Get sub-array of first n elements after shuffled
  let selected = shuffled.slice(0, n);

  return selected;
}

export default function Videos() {
  const videoCount = 10;
  const [clips, setClips] = React.useState(shuffleVideos(videoCount));
  const [narrative, setNarrative] = React.useState([]);
  const context = {
    clips, setClips, narrative, setNarrative,
  };

  const videoList = clips.map((clip) => (
    <Video key={clip} name={clip} />
  ));

  return (
    <HomeScreen>
      <ShuffleButton onClick={() => setClips(shuffleVideos(videoCount))}>Shuffle</ShuffleButton>
      <Context.Provider value={context}>
        {videoList}
      </Context.Provider>
    </HomeScreen>
  );
}
